import { getRouteMetaInfo } from '../../config/routes.config';
import {Helmet} from "react-helmet";

const {
    title: DEFAULT_TITLE,
    description: DEFAULT_DESCRIPTION
} = getRouteMetaInfo('Home');


const MetaInfo = ({
                    meta = [],
                    defer = false,
                    lang = 'ru',
                    title = DEFAULT_TITLE,
                    locale = 'ru_RU',
                    description = DEFAULT_DESCRIPTION
                }) => {
    const url = window?.location.href || 'unknown';
    const APP_NAME = "Test App";
    const AUTHOR_NAME = "Minigames";


    return (
        <Helmet
            defer={defer}
    title={title}
    htmlAttributes={{ lang }}
    titleTemplate={`${APP_NAME} | %s`}
    link={[
            {
                rel: 'canonical',
                href: url
            }
            ]}
    meta={[
            {
                name: 'description',
                content: description
            },
    {
        property: 'og:description',
            content: description
    },
    {
        property: 'og:title',
            content: title
    },
    {
        property: 'og:site_name',
            content: APP_NAME
    },
    {
        property: 'og:type',
            content: 'website'
    },
    {
        property: 'og:url',
            content: url
    },
    {
        property: 'og:locale',
            content: locale
    },
    {
        property: 'og:image',
            content: `logo192.png`
    },
    {
        name: 'author',
            content: AUTHOR_NAME
    }
].concat(meta)}
    />
);
};

export default MetaInfo;
