import { onTTFB, onFCP, onLCP, onINP, Metric } from 'web-vitals/attribution';

function sendMetrics(metrics:Partial<Metric>):void {
    console.log(`Track performance metric: ${metrics.name}. Value=${metrics.value} (${metrics.rating})`);
}


const reportWebVitals = () => {
    onTTFB(sendMetrics);
    onFCP(sendMetrics);
    onLCP(sendMetrics);
    onINP(sendMetrics);
};

export default reportWebVitals;
